
'use client'
import * as Yup from 'yup';
import React from 'react'
import { useFormik } from 'formik';
import axios from 'axios';
import toast from 'react-hot-toast';

const AddProduct = () => {
    //validation schema using yup
    const AddProductSchema = Yup.object().shape({
        name: Yup.string().min(2, 'Too Short!').max(50, 'Too Long!').required('Required'),
        price: Yup.number().min(0, 'Price must be positive').required('Required'),
        description: Yup.string().min(10, 'Too Short!').max(500, 'Too Long!').required('Required'),
        brand: Yup.string().min(2, 'Too Short!').max(50, 'Too Long!').required('Required'),
        image: Yup.mixed().required('Required')

    })



    const AddProductForm = useFormik({
        initialValues: {
            name: '',
            price: '',
            description: '',
            brand: '',
            image: ''
        },

        onSubmit: (values, { resetForm }) => {
            console.log(values);
            axios.post('http://localhost:5000/product/add', values)
                .then((result) => {

                    toast.success("Product added successfully");
                    resetForm();

                }).catch((err) => {
                    toast.error("Something went wrong");
                    console.log(err);

                });
        },
        validationSchema: AddProductSchema
    })

    console.log(AddProductForm.errors);

    const uploadFile = (e) => {
        const file = e.target.files[0];
        console.log(file);
        const formData = new FormData();
        formData.append('file', file);
        formData.append('cloud_name', "dn1wpxm8j");
        formData.append('upload_preset', "Bhoomi");

        axios.post("https://api.cloudinary.com/v1_1/dn1wpxm8j/image/upload", formData)
            .then((result) => {
                console.log(result.data);
                toast.success("Image Uploaded Successfully");
                AddProductForm.setFieldValue('image', result.data.url);

            }).catch((err) => {
                console.log(err);
                toast.error("Failed to upload image");


            });

    }
    return (
        <div className='min-h-screen flex items-center justify-center bg-gray-100'>
            {/* form product page */}
            <form
                className="bg-white p-8 rounded shadow-md w-full max-w-md"
                onSubmit={AddProductForm.handleSubmit}
            >
                <h2 className="text-2xl font-bold mb-6 text-gray-800">Add Product</h2>
                {/* name */}
                <label htmlFor="name" className="block mb-2 font-semibold text-gray-700">
                    Name
                </label>
                <input
                    type="text"
                    id="name"
                    value={AddProductForm.values.name}
                    onChange={AddProductForm.handleChange}
                    name="name"
                    required=""
                    className="w-full mb-4 px-3 py-2 border border-gray-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
                    placeholder="Product name"
                />
                {/* price */}
                <label htmlFor="price" className="block mb-2 font-semibold text-gray-700">
                    Price
                </label>
                <input
                    // type='number'
                    id='price'

                    value={AddProductForm.values.price}
                    onChange={AddProductForm.handleChange}


                    className="w-full mb-4 px-3 py-2 border border-gray-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500"

                />
                {/* description */}
                <label
                    htmlFor="description"
                    className="block mb-2 font-semibold text-gray-700"
                >
                    Description
                </label>
                <textarea
                    id="description"
                    name="description"

                    value={AddProductForm.values.description}
                    onChange={AddProductForm.handleChange}
                    rows={4}
                    className="w-full mb-4 px-3 py-2 border border-gray-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
                    placeholder="Product description"

                />
                {/* brand */}
                <label htmlFor="brand" className="block mb-2 font-semibold text-gray-700">
                    Brand
                </label>
                <input
                    type="text"
                    id="brand"
                    name="brand"
                    value={AddProductForm.values.brand}
                    onChange={AddProductForm.handleChange}

                    className="w-full mb-4 px-3 py-2 border border-gray-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
                    placeholder="Brand name"
                />
                {/* image */}
                <label htmlFor="images" className="block mb-2 font-semibold text-gray-700  ">
                    Upload Images
                </label>
                <input
                    type="file"

                    onChange={uploadFile}


                    accept="image/*"
                    className="w-full mb-6 border border-gray-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
                {/* button */}
                <button
                    type="submit"
                    className="w-full bg-blue-600 text-white font-semibold py-2 rounded hover:bg-blue-700 transition"
                    accept="image/*"
                >
                    Add Product
                </button>
            </form>


        </div>
    )
}

export default AddProduct
